


/*
*  
*/
export function findComponentsDownward(context, componentName) {
    return context.$children.reduce((components, child) => {
        if (child.$options.name === componentName) components.push(child);
        const foundChilds = findComponentsDownward(child, componentName);
        return components.concat(foundChilds);
    }, []);
}

/*
*  
*/
export function findComponentsUpward(context, componentName) {
    let parents = [];
    const parent = context.$parent;
    if (parent) {
        if (parent.$options.name === componentName) parents.push(parent);
        return parents.concat(findComponentsUpward(parent, componentName));
    } else {
        return [];
    }
}

/*
*  
*/
export function findComponentUpward(context, componentName, componentNames) {
    if (typeof componentName === 'string') {
        componentNames = [componentName];
    } else {
        componentNames = componentName;
    }

    let parent = context.$parent;
    let name = parent.$options.name;
    while (parent && (!name || componentNames.indexOf(name) < 0)) {
        parent = parent.$parent;
        if (parent) name = parent.$options.name;
    }
    return parent;
}


import {camelCase, oneOf} from "./utils";

/*
* 
*/
export function moveCursorToEnd(obj) {
    if (window.getSelection) {//ie11 10 9 ff safari
        obj.focus(); //ff
        let range = window.getSelection();//range
        range.selectAllChildren(obj);//range obj
        range.collapseToEnd();//
    }
    else if (document.selection) {//ie10 9 8 7 6 5
        let range = document.selection.createRange();//
        //let range = document.body.createTextRange();
        range.moveToElementText(obj);//rangeobj
        range.collapse(false);//
        range.select();
    }
}

/*----------------------------------------------------------------------------------*/

/*
* 
*/
export function onPasteImage(obj, handler) {
    obj.addEventListener('paste', function (event) {
        // 
        let clipboardData = event.clipboardData;
        let i = 0, items, item, types;

        if (clipboardData) {
            items = clipboardData.items;
            if (!items) return;
            item = items[0];
            // 
            types = clipboardData.types || [];
            for (; i < types.length; i++) {
                if (types[i] === 'Files') {
                    item = items[i];
                    break;
                }
            }
            // 
            if (item && item.kind === 'file' && item.type.match(/^image\//i)) {
                // 
                imgReader(item, handler);
            }
        }
    });
}

/*----------------------------------------------------------------------------------*/

/*
* 
*/
function imgReader(target, handler) {
    let file = target.getAsFile(),
        reader = new FileReader();
    reader.onload = function (e) {
        handler(e.target.result)
    };
    reader.readAsDataURL(file);
}

/*----------------------------------------------------------------------------------*/

/*
* class
*/
export function hasClass(cls, clsList) {
    return oneOf(cls, clsList.value.split(' '));
}

/* istanbul ignore next */
export function addClass(el, cls) {
    if (!el) return;
    let curClass = el.className;
    const classes = (cls || '').split(' ');

    for (let i = 0, j = classes.length; i < j; i++) {
        const clsName = classes[i];
        if (!clsName) continue;

        if (el.classList) {
            el.classList.add(clsName);
        } else {
            if (!hasClass(el, clsName)) {
                curClass += ' ' + clsName;
            }
        }
    }
    if (!el.classList) {
        el.className = curClass;
    }
}

